<template>
  <div class="main">
    <div class="logintop">
      <div class="logintop-left" @click="goBack">
        <span class="iconfont icon-zuo"></span>
      </div>
      <div class="logintop-midd">会员登录</div>
      <div class="logintop-right" @click="gotoReg">注册</div>
    </div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[
          { required: true, message: '请填写用户名' },
          {
            pattern: /^[a-z_$][0-9a-z_$]{3,9}$/i,
            message: '不能以数字开头 由数字字母_$组成 4-10位',
          },
        ]"
      />
      <van-field
        v-model="password"
        type="password"
        name="userpwd"
        label="密码"
        placeholder="密码"
        :rules="[
          { required: true, message: '请填写密码' },
          {
            pattern: /^[0-9a-z]{5,11}$/i,
            message: '密码以数字字母组成 6-12位',
          },
        ]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit" color="#bd1a21"
          >登录</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import userModel from "@/model/userModel";
import mixin from "@/mixin";
import { mapActions } from "vuex";
export default {
  mixins:[mixin],
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    ...mapActions(["getUserInfo"]),
    ...mapActions("shoppingcar",["initList"]),
    async onSubmit(info) {
      let { data: list } = await userModel.query({
        username: info.username,
        userpwd: info.userpwd,
      });
      if (list.length) {
        this.$toast.success("登录成功");
        window.localStorage.setItem("userinfo",JSON.stringify(list[0]));
        await this.getUserInfo()
        await this.initList(this.$store.state.userinfo.id)
        this.$router.go(-1);
      } else {
        this.$toast.fail("用户名或密码有误");
      }
    },
    gotoReg() {
      this.$router.push("/register");
    },
    goBack() {
      this.$router.push("/home");
    },
  },
};
</script>

<style lang="scss" scoped>
.main {
  .logintop {
    width: 100%;
    height: 45px;
    background: #bd1a21;
    display: flex;
    text-align: center;
    color: #fff;
    line-height: 45px;
    .logintop-left {
      width: 45px;
      .icon-zuo {
        font-size: 16px;
        margin-left: 15px;
      }
    }
    .logintop-midd {
      font-size: 18px;
      flex: 1;
    }
    .logintop-right {
      width: 45px;
      font-size: 14px;
    }
  }
}
</style>